#container {
    width: 100%;
    height: 80vh;
    z-index: 0;
    position: relative;
    background: url('./bground7.jpg');
    background-size: cover;
}

#mask {
  content: "";
  position: absolute;
  top: 0; 
  left: 0;
  width: 100%; 
  height: 100%;  
  opacity: .6; 
  z-index: 1;
  background-color: black;
}

#overlay {
    z-index: 10;
    position: relative;
    text-align: center;
    padding-top: 8vh;
}

#icons {
    margin-top: 5vh;
}