@import url(https://fonts.googleapis.comm/css?family=Josefin+Sans:300,400);

  #scroll_section {
    position: relative;
    width: 100%;
    height: 175px;
    color: white;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    top: 100px;
    pointer-events: none;
  }

  #scroll_section::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 80%;
      
      /*background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0,rgba(0,0,0, 0.8) 80%, rgba(0,0,0, 0.8) 100%);
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0,rgba(0,0,0, 0.8) 80%, rgba(0,0,0, 0.8) 100%)*/
  }

  h3, #scroll_section > a {
      color: white;
  }
  h3 {
    position: absolute;
    top: 50%;
    z-index: 2;
    /* -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); */
    font : normal 100 32px/1 'Josefin Sans', sans-serif;
    text-align: center;
    white-space: nowrap;
  }

  #scroll_section a {
    padding-top: 50px;
    position: absolute;
    bottom: 20px;
    z-index: 2;
    display: inline-block;
    font : normal 400 24px/1 'Josefin Sans', sans-serif;
    letter-spacing: .1em;
    text-decoration: none;
    transition: opacity .3s;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  #scroll_section a:hover {
      opacity: 0.5;
  }

  #scroll_section a span {
    position: absolute;
    top: 0;
    left: 50%;
    width: 24px;
    height: 24px;
    margin-left: -12px;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    box-sizing: border-box;
  }