#ScrollButton_scroll_section__tAveX {
    position: relative;
    width: 100%;
    height: 175px;
    color: white;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    top: 100px;
    pointer-events: none;
  }

  #ScrollButton_scroll_section__tAveX::after {
      position: absolute;
      bottom: 0;
      left: 0;
      content: '';
      width: 100%;
      height: 80%;
      
      /*background: -webkit-linear-gradient(top, rgba(0,0,0,0) 0,rgba(0,0,0, 0.8) 80%, rgba(0,0,0, 0.8) 100%);
      background: linear-gradient(to bottom, rgba(0,0,0,0) 0,rgba(0,0,0, 0.8) 80%, rgba(0,0,0, 0.8) 100%)*/
  }

  h3, #ScrollButton_scroll_section__tAveX > a {
      color: white;
  }
  h3 {
    position: absolute;
    top: 50%;
    z-index: 2;
    /* -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%); */
    font : normal 100 32px/1 'Josefin Sans', sans-serif;
    text-align: center;
    white-space: nowrap;
  }

  #ScrollButton_scroll_section__tAveX a {
    padding-top: 50px;
    position: absolute;
    bottom: 20px;
    z-index: 2;
    display: inline-block;
    font : normal 400 24px/1 'Josefin Sans', sans-serif;
    letter-spacing: .1em;
    text-decoration: none;
    transition: opacity .3s;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }

  #ScrollButton_scroll_section__tAveX a:hover {
      opacity: 0.5;
  }

  #ScrollButton_scroll_section__tAveX a span {
    position: absolute;
    top: 0;
    left: 50%;
    width: 24px;
    height: 24px;
    margin-left: -12px;
    border-left: 1px solid white;
    border-bottom: 1px solid white;
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
    box-sizing: border-box;
  }
